html {
  height: 100%;
  box-sizing: border-box;
  
}
*, *:before, *:after {
box-sizing: inherit;
}
body {
  font-family: arial,helvetica, sans-serif;
  height: 100%;
  margin: 0;
  /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#e570e7+0,c85ec7+47,a849a3+100;Pink+3D+%233 */
  background: #e570e7; /* Old browsers */ /* FF3.6-15 */
  background: -webkit-linear-gradient(top, #e570e7 0%,#c85ec7 47%,#a849a3 100%); /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to bottom, #e570e7 0%,#c85ec7 47%,#a849a3 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  background-repeat: no-repeat;
  background-attachment: fixed;
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#e570e7', endColorstr='#a849a3',GradientType=0 );
  margin-bottom: 10rem;
}
header {
text-align: center;
padding: 20px 0;
}
div.contenedor {
  max-width: 800px;
  width: 95%;
  margin: 0 auto;
}
div.hero img {
  width: 100%;
}
h2 {
text-align: center;
color: white;
text-transform: uppercase;
}
.navegacion {
    display: flex;
    justify-content: space-between;
    margin-top: 1rem;
    padding-bottom: 3rem;
}
.navegacion a {
    font-size: 1rem;
    color: white;
    text-decoration: none;
    text-transform: uppercase;
}
.navegacion a.activo {
    color: #ffce00;
}
div.productos {
    margin-top: 30px;
}
ul.lista-productos {
    list-style: none;
    margin:0;
    padding:0;
}
ul.lista-productos li{
    padding: 20px;
    text-align: center;
}
@media  (min-width:768px) {
    ul.lista-productos li{
        float: left;
        width: 50%;
    }
}

ul.lista-productos li:nth-child(2n+1) {
    clear: both;
}
ul.lista-productos li img {
    border: 10px solid rgba(128,1,195,.45);
    max-width: 100%;
}
ul.lista-productos li img:hover {
    border: 10px solid #8001c3;
}
ul.lista-productos li p {
    font-size: 28px;
    font-weight: bold;
    color: #ffffff;
    margin:10px 0 0 0;
}
ul.lista-productos li p span {
    color: #ffce00;
    font-size: 40px;
}
ul.lista-productos li a {
    background-color: #ffce00;
    padding: 10px 40px;
    display: inline-block;
    margin-top: 10px;
    color: #8001c3;
    text-decoration: none;
    font-weight: bold;
    text-transform: uppercase;
    font-size: 18px;
}

.buscador {
    background-color: transparent;
    padding: 0;
}

.buscador input[type="text"] {
    height: 3rem;
    font-size: 1.4rem;
    padding: 1rem;
    width: 100%;
    margin: 0;
}
@media (min-width: 480px) {
    .info-producto {
        display: grid;
        grid-template-columns: 1fr 2fr;
        grid-gap: 2rem;
    }
  } 
  
  .info-producto img {
    border: .5rem solid #8001c3;
    width: 100%;
  }
  .info-producto .info h2{
    text-align: left;
    text-transform: none;
    font-size: 3.4rem;
    margin: 0 0 2rem 0;
  }
  .info-producto .info .precio {
    font-size: 2.4rem;
    color: #ffce00;
    font-weight: bold;
    margin: 0 0 2rem 0;
  }
  .info-producto .info p {
    color: white;
    line-height: 2rem;
  }
@media (min-width:480px) {
    .contenedor-nosotros {
        display: grid;
        grid-template-columns: 40% 60%;
        grid-gap: 1rem;
    }
}

.contenedor-nosotros .imagen img {
    width: 100%;
}
.contenedor-nosotros .contenido h2 {
    border-bottom: 8px solid #ffce00;
    padding-bottom: 1rem;
    text-align: left;
}
.contenedor-nosotros .contenido p {
    color: white;
    line-height: 2rem;
}
form {
    border: .5rem solid #8001c3;
    background-color: white;
    padding: 2rem;
}
form legend {
    text-align: center;
    font-size: 1.8rem;
    font-weight: bold;
    margin-bottom: 2rem;
    color: #8001c3;
    text-transform: uppercase;
}

.input-field {
    margin-bottom: 2rem;
    display: flex;
    align-items: center;
}
.input-field label {
    flex: 0 0 150px;
    font-weight: bold;
}
.input-field input[type="email"],
.input-field input[type="text"],
.input-field textarea{
    flex:1 1;
    padding: 1rem;
}
.input-field input[type="submit"] {
    background-color: #8001c3;
    color: white;
    padding: .5rem 2rem;
    border: none;
    font-size: 1.2rem;
    font-weight: bold;
}
.input-field input[type="submit"]:hover {
    cursor: pointer;
    background-color: #ffce00;
    color: #8001c3;
}
.enviar {
    justify-content: flex-end;
}
